	module.exports = {"data":{"markdownRemark":{"html":"<h3>Swag</h3>\n<p>We now enable programs to award hackers with swag or physical objects.</p>\n<h3>Hackbot: Duplicate Detection</h3>\n<p>Hackbot is now able to detect duplicate and related reports to help programs associate and close reports more quickly.</p>\n<h3>Self-Close Reports</h3>\n<p>We now enable hackers to self-close their own reports if they discover that it’s no longer relevant. This won’t impact their reputation. </p>","frontmatter":{"path":"/changelog/2015/april","title":"April 2015"}}},"pathContext":{}}

